// 
const ProductOperateType = [{
  id: 0,
  name: ""
}, {
  id: 1,
  name: ""
}, {
  id: 2,
  name: "OEM" // OEM
}]

// 
const ProductAttribute = [{
  name: "",
  id: 0
}, {
  name: "",
  id: 1
}, {
  name: "",
  id: 2
}];

export default {
  /**
   * 
   * @param {*} belong 
   */
  getProductOperateType(belong) {
    return ProductOperateType.filter(item => {
      if (belong != 3) {
        return item.id !== 2;
      }
      return true;
    });
  },
  /**
   * 
   */
  getProductAttribute() {
    return ProductAttribute;
  }
}



